@import '../../assets/css/theme';
@import '../../assets/css/var';
@import '../../assets/css/mixins';

.tab_content_main {
  margin-top: 40px;
  padding: 30px 40px;
  border-radius: 10px 10px;
  background-color: $color-card-bg-active;
  @include mobile-only {
    padding: 20px 10px 12px;
  }
}

.light_with_border {
  @include darkThemeOnly {
    border: none;
  }
  @include lightThemeOnly {
    border: 1px solid $color-border-secondary;
  }
}

.content_title {
  font-size: 20px;
  font-weight: 600;
  color: $color-text-default;
  line-height: 28px;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
  margin-top: 40px;

  & > span {
    &:last-child {
      margin-right: 0;
      margin-top: 12px;
    }
  }

  &_left {
    display: flex;
    align-items: center;
    margin-right: auto;

    &_img {
      display: block;
      width: 14px;
      height: 14px;
      margin-right: 6px;
    }

    &_text {
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      color: $color-text-default-5;
      @include mobile-only {
        font-size: 12px;
        line-height: 17px;
        font-weight: 400;
      }
    }
  }
  &_right {
    display: flex;
    align-items: center;
    @include mobile-only {
      margin-top: 10px;
    }
  }
  &_effect {
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    margin-right: 30px;
    @include mobile-only {
      display: block;
      font-size: 12px;
      line-height: 17px;
      margin: 12px 0 23px;
      font-weight: 400;
      text-align: center;
    }
  }
  &_effect2 {
    margin-right: 0;
  }
  &_lock_link {
    display: flex;
    align-items: center;
    @include mobile-only {
      font-weight: 400 !important;
      font-family: PingFangSC-Regular, PingFang SC;
    }
  }
}

.vote_form {
  display: flex;
  input {
    &::placeholder {
      color: $color-text-default-3 !important;
    }
  }
  :global {
    .ant-select-selector {
      @include mobile-only {
        border-radius: 5px !important;
      }
    }
    .ant-input-affix-wrapper {
      @include mobile-only {
        border-radius: 5px !important;
      }
    }
    .ant-input-affix-wrapper-status-error:not(.ant-input-affix-wrapper-disabled):not(
        .ant-input-affix-wrapper-borderless
      ).ant-input-affix-wrapper
      .ant-input-suffix {
      color: $color-text-default;
    }
    .ant-form-item {
      &:first-child {
        flex: 1;
        width: 0;
        padding-right: 20px;
      }
      .ant-form-item-label label {
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: $color-text-default;
      }
    }
    .ant-select-selection-item {
      padding-left: 5px !important;
      line-height: 22px;
      img {
        width: 20px;
        height: 20px;
        margin-right: 5px;
        object-fit: cover;
        border-radius: 2px;
      }
    }
    .ant-form-item .ant-form-item-explain-error {
      font-size: 12px;
      color: #ff8700;
    }
    .ant-select-open {
      .ant-select-selection-item {
        color: $color-text-default-3 !important;
      }
      img {
        opacity: 0.3;
      }
    }
    .ant-select-selection-placeholder {
      color: $color-text-default-3 !important;
    }
  }
}
.vote_infoBox {
  @include mobile-only {
    border-radius: 5px !important;
  }
  > div {
    > div:nth-child(1) {
      font-size: 14px;
    }
    > div:nth-child(2) {
      font-size: 24px;
      margin-top: 20px;
    }
  }
}
.combine_icon {
  span {
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 14px;
    color: $color-text-default;
    line-height: 20px;
  }
  img {
    width: 30px;
    height: 30px;
    margin-right: 21px;
    object-fit: cover;
    border-radius: 2px;
  }
  &:hover {
    span {
      color: #c1a0ff;
      @include lightThemeOnly {
        color: #722fff;
      }
    }
  }
}
:global {
  .ant-select-item-option-selected {
    span {
      color: #c1a0ff !important;
      @include lightThemeOnly {
        color: #722fff;
      }
    }
  }
}

.legend_wrap {
  display: flex;
  flex-wrap: wrap;
  .echarts_legend_item {
    width: 50%;
  }
}

