.rank_title {
  margin-top: 60px;
  font-family: PingFangSC, PingFang SC;
  font-weight: 600;
  font-size: 30px;
  color: $color-text-default;
  line-height: 42px;
  text-align: center;
}

.rank_row {
  display: flex;
  align-items: center;
  padding: 0 20px;
  height: 35px;
  line-height: 35px;
  background: $color-card-bg-active;
  border-radius: 5px;
  span {
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 12px;
    color: $color-text-default-3;
  }
  &_1 {
    width: 42px;
    text-align: center;
  }
  &_2 {
    padding-left: 30px;
    width: 90px;
  }
  &_3 {
    padding-left: 20px;
    // width: 168px;
    width: 228px;
  }
  &_4 {
    padding-left: 10px;
    width: 210px;
  }
  &_5 {
    width: 568px;
  }
  &_6 {
    padding-left: 20px;
    img {
      display: block;
      width: 20px;
      height: 20px;
    }
  }
}

.rank_row_logo {
  width: 60px;
  height: 60px;
  @include mobile-only {
    width: 50px;
    height: 50px;
  }
  img {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    object-fit: cover;
  }
}

.rank_flag {
  width: 20px;
  height: 20px;
  background: url(../../assets/images/pump/ico_flag.svg) no-repeat 0 0;
  background-size: 100% 100%;
  &:hover {
    background-image: url(../../assets/images/pump/ico_flag_hover.svg);
    @include lightThemeOnly {
      background-image: url(../../assets/images/pump/ico_flag_light_hover.svg);
    }
  }
  @include lightThemeOnly {
    background-image: url(../../assets/images/pump/ico_flag_light.svg);
  }
}

.rank_max_row {
  background-image: url(../../assets/images/pump/ico_flag_active.svg) !important;
  &:hover {
    background-image: url(../../assets/images/pump/ico_flag_active_hover.svg) !important;
  }
}

.rank_header_row {
  margin-bottom: 10px;
  @include mobile-only {
    display: none;
  }
}
.rank_list_main {
  .rank_list_main_inner {
    min-height: 200px;
  }

  .rank_main_row {
    margin-bottom: 10px;
    padding: 10px 20px;
    height: 80px;
    cursor: pointer;
    @include lightThemeOnly {
      &:hover {
        box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
        background: #fff;
      }
    }
    @include mobile-only {
      display: none;
    }
    &:hover {
      background: #403858;
      span {
        color: #c1a0ff;
        @include lightThemeOnly {
          color: #722fff;
        }
      }
      .rank_percent_top {
        color: #c1a0ff;
        @include lightThemeOnly {
          color: #722fff;
        }
      }
    }
    span {
      color: $color-text-default;
      font-size: 16px;
    }
  }

  .rank_row_mobile {
    display: none;
    padding: 10px;
    margin-bottom: 10px;
    background: $color-card-bg-active;
    border-radius: 5px;
    overflow: hidden;
    @include mobile-only {
      display: block;
    }
    .rank_row_mobile_t {
      position: relative;
      display: flex;
      align-items: center;
      padding-right: 40px;
      .rank_row_mobile_rank_num {
        position: absolute;
        right: 0;
        top: 0;
        img {
          width: 40px;
          height: 40px;
        }
        span {
          position: relative;
          top: -10px;
          right: -10px;
          display: inline-block;
          padding: 0 5px;
          min-width: 44px;
          height: 30px;
          line-height: 30px;
          background: rgba(140, 81, 255, 0.3);
          border-radius: 0px 5px 0px 10px;
          white-space: nowrap;
          color: #fff;
          text-align: center;
          @include lightThemeOnly {
            color: #5a14e1;
          }
        }
      }
      .rank_row_mobile_r {
        margin-left: 10px;
        .rank_row_mobile_r_tokenName {
          font-family: PingFangSC, PingFang SC;
          font-weight: 600;
          font-size: 16px;
          color: $color-text-default;
          line-height: 22px;
        }
        .rank_row_mobile_r_tokenAddress {
          margin-top: 8px;
          font-family: PingFangSC, PingFang SC;
          font-weight: 400;
          font-size: 14px;
          color: $color-text-default-5;
          line-height: 20px;
        }
      }
    }
    .rank_row_mobile_b {
      position: relative;
      margin-top: 12px;
      padding-right: 30px;
      .rank_flag {
        position: absolute;
        right: 0;
        bottom: 0;
      }
    }
  }
}

.rank_percent {
  .rank_percent_top {
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: $color-text-default;
    text-align: right;
    @include mobile-only {
      font-size: 14px;
      line-height: 20px;
      text-align: left;
    }
  }
  .rank_percent_btm {
    margin-top: 12px;
    @include mobile-only {
      margin-top: 4px;
    }
    .rank_percent_bar {
      position: relative;
      height: 6px;
      background: #4d4366;
      border-radius: 6px;
      overflow: hidden;
      @include mobile-only {
        height: 4px;
        border-radius: 4px;
      }
      @include lightThemeOnly {
        background: #f5f5f5;
      }
    }
    .rank_percent_bar_inner {
      position: absolute;
      left: 0;
      height: 6px;
      border-radius: 6px;
      background: linear-gradient(90deg, #ff529f 0%, #6901ff 100%);
      @include mobile-only {
        height: 4px;
        border-radius: 4px;
      }
    }
  }
}

:global(#root) {
  .rank_pagination {
    padding: 30px;
    text-align: center;
    @include mobile-only {
      padding: 20px;
    }
    :global {
      .ant-pagination-item a {
        color: $color-border-pagination;
      }
      .ant-pagination {
        & > .ant-pagination-item {
          margin-left: 8px;
          border: 1px solid $color-border-pagination !important;
          color: $color-border-pagination !important;
          overflow: hidden;
        }

        .ant-pagination-item-1 {
          margin-left: 0;
        }
      }

      .ant-pagination-item-active {
        background: #6d00f4;

        border-color: rgba(109, 0, 244, 1);
        a {
          color: rgba(255, 255, 255, 1);
          &:hover {
            color: rgba(255, 255, 255, 1);
          }
        }
      }

      .ant-pagination-jump-next,
      .ant-pagination-jump-prev {
        min-width: 24px;
        margin-left: 8px;
        border-radius: 5px;
        border: 1px solid $color-border-pagination;
        color: $color-border-pagination;
        .ant-pagination-item-link {
          color: rgba($color-text-default, 0.5);
          .ant-pagination-item-ellipsis {
            font-size: 12px;
            color: rgba($color-text-default, 0.5);
          }
          .ant-pagination-item-container {
            .ant-pagination-item-link-icon {
              margin-left: 0;
              color: rgba($color-text-default, 0.5);
            }
          }
        }
      }
      .ant-pagination .ant-pagination-disabled {
        opacity: 0.25;
      }
    }
  }
}

.rank_list_empty {
  // padding: 100px 0;
  text-align: center;
  background: $color-card-bg-active;
  border-radius: 5px;

  .rank_list_empty_img {
    margin: 0 auto;
    width: 100px;
    img {
      display: block;
      width: 100%;
    }
  }
  span {
    line-height: 22px;
    font-size: 16px;
    color: $color-text-default-7;
  }
}

@include mobile-only {
  .tab_content_main {
    margin-top: 20px;
    padding: 10px 10px 16px;
  }
  .vote_form {
    display: block;
    :global(.ant-form-item):first-child {
      width: initial;
      padding-right: 0;
    }
  }
  .content_title {
    display: block;
    font-size: 16px;
    margin-top: 30px;
    margin-bottom: 14px;
  }
  .content_title_2 {
    display: flex;
    flex-direction: column-reverse;
    font-size: 12px;
    // margin-top: 12px;
    margin-top: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-family: 'PingFangSC-Regular';
  }
  div.light_with_border {
    padding: 10px 10px 12px;
  }
  .vote_infoBox {
    height: initial !important;
    padding: 10px 10px 12px;
    > div {
      &:nth-child(2) {
        margin-bottom: 0 !important;
      }
      margin-bottom: 12px !important;
      > div:nth-child(2) {
        font-size: 14px;
        margin-top: 0;
      }
    }
  }
  // new
  .title {
    .title_t {
      font-size: 20px;
      line-height: 30px;
    }
    .title_b {
      margin-top: 10px;
      font-size: 12px;
      line-height: 20px;
    }
  }
  .banner {
    margin-top: 21px;
  }

  .rank_title {
    margin-top: 40px;
    font-size: 20px;
    line-height: 28px;
  }
}

.pool_title {
  font-size: 14px;
  font-weight: 600;
  color: $color-text-default;
  line-height: 20px;
  margin-left: 16px;
}
